import TabBar from '@/components/TabBar/index.vue'
import LeftMenu from '@/components/LeftMenu/index.vue' 
import $ from 'jquery'

export default {
    data() {
        return {
           article:{}
        }
    },
    components: {
        TabBar,
        LeftMenu
    },
    created: function () {
     
        $.ajax({
            type: "GET",
            url: "/Mobile/Api/QianDetail",
            data: {},
            dataType: "json",
            success: function(rst){
                if(rst.status==0){
                    this.article = rst.data;
                }
                else{
                    alert(rst.msg);
                }
            }
        });
    }
}